.login {
  position: relative;
  width: 100vw;
  height: 100vh;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.login-container { 
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.login-container h1 {
  text-align: center;
  margin-bottom: 20px;
  color: orangered;
}

.login-container form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-container input[type='text'],
.login-container input[type='password'],
.login-container button {
  width: calc(100% - 22px);
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  box-sizing: border-box;
}

.login-container button {
  margin-right: 15px;
}

.login-container p {
  text-align: center;
  margin-top: 15px;
  margin-left: -5px;
  font-size: 14px;
}

.login-container a {
  color: #007bff;
  text-decoration: none;
}

.login-container a:hover {
  text-decoration: underline;
}

/* New CSS to hide overflow for the login page */
body.login-page {
  overflow: hidden;
}
