.contact-container {
    position: relative;
    width: 100vw;
    height: 400px;
  }
  
  .contact-container .header {
    position: relative;
  }
  
  .contact-container .header img {
    width: 100%;
    height: 400px;
    object-fit: cover;
  }
  
  .contact-container h2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    color: white; /* Adjust text color as needed */
    text-align: center;
    font-weight: bolder;
    color:orangered;
    font-size: 40px;
  }

  body.contact-page{
    overflow-x: hidden;
  }
  
  .location h1{
    text-align: center;
  }

  .location iframe{
    width:70%;
    margin-left:225px;
    margin-top:50px;
  }


  .contact-form {
    margin: 0 auto;
}

.contact-form h1{
  text-align: center;
  width:90%;
}

.contact-form form{
  margin-left:300px;
  margin-top: 20px;
}

.form-group {
    margin-bottom: 12px;
}

.form-group label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
}

.form-group input, textarea {
    width: 900px;
    padding: 10px;
    box-sizing: border-box;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 5px;
}

.form-group textarea {
    resize: vertical;
}

.form-group button {
    background-color: #4CAF50;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.form-group button:hover {
    background-color: #45a049;
}
