.help-container {
    position: relative;
    width: 100vw;
    height: 400px;
  }
  
  .help-container .header {
    position: relative;
  }
  
  .help-container .header img {
    width: 100%;
    height: 400px;
    object-fit: cover;
  }
  
  .help-container h1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    color: white; /* Adjust text color as needed */
    text-align: center;
    font-weight: bolder;
    color:orangered;
    font-size: 40px;
  }

  body.help-page{
    overflow-x: hidden;
  }

/* Add these styles to your Help.css file */
.chat {
  position: relative;
}

.chat-button {
  position: absolute;
  top: 110%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
}

.hide {
  display: none;
}

.show {
  display: block;
}
.chat h2{
  text-align: center;
  margin-top:40px;
}

.chat p{
  width:80%;
  margin-left:170px;
  color:black;
}
.chatbot {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);

}



.emergency .head{
  margin-top:40px;
  text-align: center;
  color:green;
}

.emergency {
  text-align: center;
}


.section {
  display: inline-block; /* Ensures each section is displayed as a block */
  background-color: #fff;
  padding: 20px;
  margin: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: left;
  width:900px;
}

.section h3 {
  color: #555;
  font-size: 22px;
  margin-bottom: 10px;
}

.section p {
  color: #666;
  font-size: 16px;
  margin-bottom: 20px;
}

.section ul {
  list-style-type: none;
  padding: 0;
}

.section ul li {
  display: block; /* Displays list items as block elements */
  margin-bottom: 10px;
  font-size: 16px;
}

