
.container {
    display: block;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Adjust as needed */

  }
  
  .hero {
    position: relative;
    text-align: center;
    width:100%;
    height:600px;
    margin-bottom:250px;
  }
  
  .hero img {
    width: 100%;
    height: 100%;
  }
  
  .hero h1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 36px;
    font-weight:bolder;
    width: 600px;
    height: 100px;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .health {
    color: orangered; /* Color for "health" */
    margin-right: 5px; /* Add margin to create space */
    margin-left:5px;
  }
  
  .wealth {
    color: green; /* Color for "wealth" */
    margin-left: 5px; /* Add margin to create space */
  }
  
  
  
  
  .hero button {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-bottom: 170px;
  }
  
  .hero button:hover {
    background-color: #0056b3;
  }
  
 
  
  .appointment {
    margin-top:-200px;
    text-align: center;
