  .slide {
    position: relative;
  }
  
  .slide img {
    width: 100%;
    height: 500px;
    transition: opacity 0.5s; /* Adding transition for opacity */
  }
  
  .slide:hover img {
    opacity: 0.8; /* Decrease opacity on hover */
  }
  
  .content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: opacity 0.5s;
    width:80%;
    margin-right:30px;
  }
  
  .slide:hover .content {
    opacity: 1;
  }
  
  .content h1 {
    color: white;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 10px 20px;
    border-radius: 5px;
    margin: 0;
  }
  
  .content h2 {
    color: red; /* Change color of h2 */
    font-size: 24px; /* Adjust font size */
    margin-bottom: 10px; /* Add some spacing */
    animation: fadeInUp 1s ease; /* Add animation */
  }
  .content h2:hover{
    transform: scale(1.3);
  }
  
  .content p {
    color:#333;

  }
  

  
  .carousel-root .carousel .control-dots {
    display: none;
  }
  

